import React, { useState } from 'react'
import ChooseCourses from '../ChooseCourses/ChooseCourses';
import TeacherGrades from '../TeacherGrades/TeacherGrades'

export default function GradesTable({studentsList, selectedUserIndex,courses ,addCourses}) {
  
    // const [newStudent,setNewstudent] = useState(false);
    let newStudent = false;
    if(!studentsList[selectedUserIndex].courses) {
        newStudent = true;
    }
  
    return (
        <>
            <thead>
                <tr>                       
                    <th>ציון</th> 
                    <th>שם הקורס</th>                    
                    <th>קורס</th>
                </tr>
            </thead>
            <tbody className="students-body">
                { !newStudent ?
                    studentsList[selectedUserIndex].courses.map(one => 
                        <tr><TeacherGrades courses={one} 
                                            coursesList={courses}/>
                        </tr>)
                    :
                   <tr> <ChooseCourses studentsList={studentsList} 
                                       selectedUserIndex={selectedUserIndex} 
                                       coursesList={courses}
                                       newStudent={newStudent}
                                       addCourses={addCourses}/>   
                   </tr>
                }     
            </tbody>
        </>
    )
}
