import React, { useState } from 'react';
import { Button, Form } from 'react-bootstrap';

export default function ChooseCourses({studentsList,selectedUserIndex,coursesList,newStudent,addCourses}) {

    const [selectedCourseIndex, setSelectedCourseIndex] = useState(0);
    const [grade,setGrade] = useState("");

    const options = coursesList.map((course) =>    
        <option value={course.courseId}>{course.courseId}</option>);

    function setIndex(event){
        setSelectedCourseIndex(event.target.value);
    }
function updateGrade(){
    // setGrade(event.target.value);
    let courses = {
        courseId: coursesList[selectedCourseIndex].courseId,
        courseName:coursesList[selectedCourseIndex].courseName,
        courseGrade: grade 
    }
    console.log(courses)
    // studentsList[selectedUserIndex].courses = courses;
    newStudent= false;
    addCourses(courses,selectedUserIndex);
    // studentsList[selectedUserIndex].courses[0].courseName =coursesList[selectedCourseIndex].courseName;
    // studentsList[selectedUserIndex].courses[0].courssGrade = grade;
}

    return (
        <>
            <td>
                <Form.Control 
                    className="p-input"
                    type="text" 
                    placeholder="" 
                    value={grade}
                    onChange={ e => setGrade(e.target.value)}/> 
            </td>                          
            <td>{coursesList[selectedCourseIndex].courseName}</td>
            <td>
                <Form.Control
                    as="select"
                    className="my-1 mr-sm-2"
                    custom
                    value={selectedCourseIndex} 
                    onChange={setIndex}>                   
                    {options}
                </Form.Control>
            </td>
            <Button className="p-login-button" type="button" onClick={updateGrade}>
                        כניסה
            </Button>  
        </>          
        
    )
}
